.myButton {
	background-color:transparent;
	border-radius:28px;
	border:1px solid #164d1c;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	text-decoration:none;
	text-shadow:0px 1px 0px #25631c;
}
.myButton:hover {
	background-color:#476e32;
}
.myButton:active {
	position:relative;
	top:1px;
}
